import React, { useState } from 'react';
import { 
  Container, 
  Typography, 
  Box, 
  Grid, 
  TextField, 
  Button, 
  Paper, 
  Divider, 
  useTheme, 
  useMediaQuery,
  IconButton,
  Accordion,
  AccordionSummary,
  AccordionDetails,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Snackbar,
  Alert
} from '@mui/material';
import { 
  Phone, 
  Email, 
  LocationOn, 
  FacebookOutlined, 
  Instagram, 
  YouTube, 
  Twitter,
  ExpandMore,
} from '@mui/icons-material';

const ContactPage: React.FC = () => {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));
  
  // State cho form lin h
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [phone, setPhone] = useState('');
  const [subject, setSubject] = useState('');
  const [message, setMessage] = useState('');
  const [alertOpen, setAlertOpen] = useState(false);
  
  // Danh sch cc ca hng
  const storeLocations = [
    {
      name: 'MINISO Aeon Mall H ng',
      address: 'Tng 1, AEON Mall H ng, Phng Dng Ni, H ng, H Ni',
      phone: '024 6666 7777',
      hours: '09:00 - 22:00'
    },
    {
      name: 'MINISO Vincom Center B Triu',
      address: 'Tng B1, Vincom Center, 191 B Triu, Hai B Trng, H Ni',
      phone: '024 8888 9999',
      hours: '10:00 - 22:00'
    },
    {
      name: 'MINISO Times City',
      address: 'Tng B1, TTTM Times City, 458 Minh Khai, Hai B Trng, H Ni',
      phone: '024 7777 8888',
      hours: '09:30 - 22:00'
    },
    {
      name: 'MINISO Aeon Mall Tn Ph',
      address: 'Tng 1, AEON Mall Tn Ph, 30 B Bao Tn Thng, Qun Tn Ph, TPHCM',
      phone: '028 6666 7777',
      hours: '09:00 - 22:00'
    }
  ];
  
  // Danh sch FAQ
  const faqs = [
    {
      question: 'Chnh sch i tr sn phm ca MINISO nh th no?',
      answer: 'MINISO p dng chnh sch i tr trong vng 7 ngy k t ngy mua hng. Sn phm i tr phi cn nguyn tem, nhn mc v ha n mua hng. Mt s sn phm nh m phm,  lt, ph kin nh s khng c p dng i tr v l do v sinh.'
    },
    {
      question: 'MINISO c ship hng ton quc khng?',
      answer: 'C, MINISO cung cp dch v giao hng ton quc cho cc n hng online. Thi gian giao hng t 2-5 ngy ty vo khu vc. n hng trn 500.000 s c min ph vn chuyn.'
    },
    {
      question: 'Lm th no  tr thnh thnh vin ca MINISO?',
      answer: 'Bn c th ng k thnh vin trc tip ti ca hng MINISO hoc thng qua ng dng MINISO Vietnam. Thnh vin s nhn c im thng cho mi ln mua sm, u i sinh nht v cc chng trnh khuyn mi c bit.'
    },
    {
      question: 'MINISO c cung cp dch v qu tng doanh nghip khng?',
      answer: 'C, MINISO cung cp dch v qu tng doanh nghip vi nhiu la chn sn phm v mc gi ph hp. Chng ti c th cung cp gii php qu tng theo yu cu ring vi s lng ln. Vui lng lin h phng kinh doanh  c t vn chi tit.'
    },
    {
      question: 'Sn phm MINISO c xut x t u?',
      answer: 'Sn phm MINISO c thit k v sn xut ti nhiu quc gia khc nhau bao gm Trung Quc, Nht Bn, Hn Quc v mt s nc ng Nam . Mi sn phm u c kim sot cht lng nghim ngt theo tiu chun quc t trc khi n tay ngi tiu dng.'
    }
  ];
  
  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    // X l gi form (trong thc t s gi API)
    console.log({ name, email, phone, subject, message });
    // Reset form
    setName('');
    setEmail('');
    setPhone('');
    setSubject('');
    setMessage('');
    // Hin th thng bo thnh cng
    setAlertOpen(true);
  };
  
  const handleAlertClose = () => {
    setAlertOpen(false);
  };

  return (
    <Container maxWidth="lg" sx={{ py: 5 }}>
      {/* Banner */}
      <Box 
        sx={{ 
          height: isMobile ? '180px' : '250px',
          backgroundImage: 'url(/images/contact-banner.jpg)',
          backgroundSize: 'cover',
          backgroundPosition: 'center',
          borderRadius: 2,
          mb: 5,
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          position: 'relative',
          '&::before': {
            content: '""',
            position: 'absolute',
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            backgroundColor: 'rgba(0,0,0,0.5)',
            borderRadius: 2
          }
        }}
      >
        <Typography 
          variant={isMobile ? "h4" : "h2"} 
          color="white" 
          align="center"
          sx={{ 
            fontWeight: 700, 
            position: 'relative',
            zIndex: 1,
            textShadow: '2px 2px 4px rgba(0,0,0,0.5)'
          }}
        >
          Lin H Vi Chng Ti
        </Typography>
      </Box>

      {/* Thng tin lin h v Form */}
      <Grid container spacing={4} sx={{ mb: 6 }}>
        {/* Thng tin lin h */}
        <Grid item xs={12} md={5}>
          <Paper elevation={2} sx={{ p: 3, height: '100%', borderRadius: 2 }}>
            <Typography variant="h5" gutterBottom color="primary" sx={{ fontWeight: 600, mb: 3 }}>
              Thng Tin Lin H
            </Typography>
            
            <Box sx={{ display: 'flex', alignItems: 'flex-start', mb: 3 }}>
              <LocationOn color="primary" sx={{ mr: 2, mt: 0.5 }} />
              <Box>
                <Typography variant="subtitle1" sx={{ fontWeight: 600 }}>
                  Vn phng chnh
                </Typography>
                <Typography variant="body2" color="text.secondary">
                  Ta nh Vinhomes Central Park, 720A in Bin Ph, Phng 22, Qun Bnh Thnh, TP.HCM
                </Typography>
              </Box>
            </Box>
            
            <Box sx={{ display: 'flex', alignItems: 'center', mb: 3 }}>
              <Phone color="primary" sx={{ mr: 2 }} />
              <Box>
                <Typography variant="subtitle1" sx={{ fontWeight: 600 }}>
                  Hotline
                </Typography>
                <Typography variant="body2" color="text.secondary">
                  1900 1234 (8:00 - 20:00)
                </Typography>
              </Box>
            </Box>
            
            <Box sx={{ display: 'flex', alignItems: 'center', mb: 4 }}>
              <Email color="primary" sx={{ mr: 2 }} />
              <Box>
                <Typography variant="subtitle1" sx={{ fontWeight: 600 }}>
                  Email
                </Typography>
                <Typography variant="body2" color="text.secondary">
                  support@miniso.vn
                </Typography>
              </Box>
            </Box>
            
            <Divider sx={{ my: 3 }} />
            
            <Typography variant="h6" gutterBottom sx={{ fontWeight: 600 }}>
              Kt ni vi chng ti
            </Typography>
            
            <Box sx={{ display: 'flex', gap: 2, mt: 2 }}>
              <IconButton color="primary" sx={{ backgroundColor: 'action.hover' }}>
                <FacebookOutlined />
              </IconButton>
              <IconButton color="primary" sx={{ backgroundColor: 'action.hover' }}>
                <Instagram />
              </IconButton>
              <IconButton color="primary" sx={{ backgroundColor: 'action.hover' }}>
                <YouTube />
              </IconButton>
              <IconButton color="primary" sx={{ backgroundColor: 'action.hover' }}>
                <Twitter />
              </IconButton>
            </Box>
          </Paper>
        </Grid>
        
        {/* Form lin h */}
        <Grid item xs={12} md={7}>
          <Paper elevation={2} sx={{ p: 3, borderRadius: 2 }}>
            <Typography variant="h5" gutterBottom color="primary" sx={{ fontWeight: 600, mb: 3 }}>
              Gi Tin Nhn Cho Chng Ti
            </Typography>
            
            <form onSubmit={handleSubmit}>
              <Grid container spacing={2}>
                <Grid item xs={12} sm={6}>
                  <TextField
                    fullWidth
                    label="H tn"
                    variant="outlined"
                    required
                    value={name}
                    onChange={(e) => setName(e.target.value)}
                  />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <TextField
                    fullWidth
                    label="S in thoi"
                    variant="outlined"
                    value={phone}
                    onChange={(e) => setPhone(e.target.value)}
                  />
                </Grid>
                <Grid item xs={12}>
                  <TextField
                    fullWidth
                    label="Email"
                    variant="outlined"
                    type="email"
                    required
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                  />
                </Grid>
                <Grid item xs={12}>
                  <FormControl fullWidth variant="outlined">
                    <InputLabel>Ch </InputLabel>
                    <Select
                      value={subject}
                      onChange={(e) => setSubject(e.target.value)}
                      label="Ch "
                    >
                      <MenuItem value="Thc mc sn phm">Thc mc sn phm</MenuItem>
                      <MenuItem value="Gp  dch v">Gp  dch v</MenuItem>
                      <MenuItem value="T vn mua hng">T vn mua hng</MenuItem>
                      <MenuItem value="t hng s lng ln">t hng s lng ln</MenuItem>
                      <MenuItem value="Hp tc kinh doanh">Hp tc kinh doanh</MenuItem>
                      <MenuItem value="Khc">Khc</MenuItem>
                    </Select>
                  </FormControl>
                </Grid>
                <Grid item xs={12}>
                  <TextField
                    fullWidth
                    label="Ni dung"
                    variant="outlined"
                    multiline
                    rows={4}
                    required
                    value={message}
                    onChange={(e) => setMessage(e.target.value)}
                  />
                </Grid>
                <Grid item xs={12}>
                  <Button 
                    type="submit" 
                    variant="contained" 
                    color="primary" 
                    size="large"
                    fullWidth={isMobile}
                    sx={{ mt: 2, px: 4 }}
                  >
                    Gi tin nhn
                  </Button>
                </Grid>
              </Grid>
            </form>
          </Paper>
        </Grid>
      </Grid>

      {/* Ca hng */}
      <Box sx={{ mb: 6 }}>
        <Typography variant="h4" align="center" gutterBottom sx={{ fontWeight: 600, mb: 4 }}>
          H Thng Ca Hng
        </Typography>
        
        <Grid container spacing={3}>
          {storeLocations.map((store, index) => (
            <Grid item xs={12} sm={6} md={3} key={index}>
              <Paper
                elevation={2}
                sx={{
                  p: 3,
                  height: '100%',
                  borderRadius: 2,
                  transition: 'transform 0.3s',
                  '&:hover': {
                    transform: 'translateY(-5px)',
                    boxShadow: 4
                  }
                }}
              >
                <Typography variant="h6" gutterBottom color="primary">
                  {store.name}
                </Typography>
                <Box sx={{ display: 'flex', mb: 1 }}>
                  <LocationOn fontSize="small" color="action" sx={{ mr: 1, mt: 0.3 }} />
                  <Typography variant="body2" color="text.secondary">
                    {store.address}
                  </Typography>
                </Box>
                <Box sx={{ display: 'flex', mb: 1 }}>
                  <Phone fontSize="small" color="action" sx={{ mr: 1, mt: 0.3 }} />
                  <Typography variant="body2" color="text.secondary">
                    {store.phone}
                  </Typography>
                </Box>
                <Box sx={{ display: 'flex' }}>
                  <Typography variant="body2" sx={{ fontWeight: 600 }}>
                    Gi m ca:
                  </Typography>
                  <Typography variant="body2" color="text.secondary" sx={{ ml: 1 }}>
                    {store.hours}
                  </Typography>
                </Box>
              </Paper>
            </Grid>
          ))}
        </Grid>
        
        <Box sx={{ textAlign: 'center', mt: 3 }}>
          <Button 
            variant="outlined" 
            color="primary"
            endIcon={<ExpandMore />}
          >
            Xem tt c ca hng
          </Button>
        </Box>
      </Box>

      {/* Bn  */}
      <Paper elevation={1} sx={{ p: 0, mb: 6, borderRadius: 2, height: 400, overflow: 'hidden' }}>
        <Typography sx={{ p: 2, backgroundColor: 'primary.main', color: 'white', fontWeight: 600 }}>
          Bn  ca hng
        </Typography>
        <Box 
          component="iframe" 
          src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3919.2891388661406!2d106.7007588759516!3d10.789454489362!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x317528cda6db542f%3A0x1499d9c8aa5fc0ac!2sVinhomes%20Central%20Park!5e0!3m2!1sen!2s!4v1689500132661!5m2!1sen!2s" 
          width="100%" 
          height="100%" 
          style={{ border: 0 }} 
          allowFullScreen 
          loading="lazy" 
          referrerPolicy="no-referrer-when-downgrade"
        />
      </Paper>

      {/* FAQs */}
      <Box sx={{ mb: 5 }}>
        <Typography variant="h4" align="center" gutterBottom sx={{ fontWeight: 600, mb: 4 }}>
          Cu Hi Thng Gp
        </Typography>
        
        <Paper elevation={2} sx={{ borderRadius: 2 }}>
          {faqs.map((faq, index) => (
            <Accordion key={index} disableGutters>
              <AccordionSummary
                expandIcon={<ExpandMore />}
                sx={{ 
                  backgroundColor: index % 2 === 0 ? 'rgba(0,0,0,0.02)' : 'transparent',
                  '&:hover': { backgroundColor: 'rgba(0,0,0,0.04)' }
                }}
              >
                <Typography variant="subtitle1" sx={{ fontWeight: 600 }}>
                  {faq.question}
                </Typography>
              </AccordionSummary>
              <AccordionDetails>
                <Typography variant="body2" color="text.secondary">
                  {faq.answer}
                </Typography>
              </AccordionDetails>
            </Accordion>
          ))}
        </Paper>
      </Box>
      
      {/* Alert thng bo */}
      <Snackbar open={alertOpen} autoHideDuration={6000} onClose={handleAlertClose}>
        <Alert onClose={handleAlertClose} severity="success" variant="filled">
          Tin nhn ca bn  c gi thnh cng! Chng ti s phn hi trong thi gian sm nht.
        </Alert>
      </Snackbar>
    </Container>
  );
};

export default ContactPage; 