import React, { useState, useEffect } from 'react';
import {
  Container,
  Typography,
  Box,
  Grid,
  Paper,
  Tabs,
  Tab,
  TextField,
  Button,
  Divider,
  Avatar,
  FormControlLabel,
  Switch,
  IconButton,
  List,
  ListItem,
  ListItemText,
  ListItemSecondaryAction,
  Radio,
  RadioGroup,
  FormControl,
  FormLabel,
  useMediaQuery,
  useTheme,
  InputAdornment,
  Snackbar,
  Alert,
  Stack,
  CircularProgress
} from '@mui/material';
import {
  Person,
  Edit,
  Visibility,
  VisibilityOff,
  LocationOn,
  Add,
  Delete,
  Check,
  Close,
  Save,
  Lock,
  Home,
  Phone,
  Email,
  PhotoCamera,
  CreditCard,
  Notifications
} from '@mui/icons-material';
import { useSnackbar } from '../components/GlobalSnackbarProvider';
import { useAuth } from '../contexts/AuthContext';
import { useNavigate } from 'react-router-dom';

interface TabPanelProps {
  children?: React.ReactNode;
  index: number;
  value: number;
}

function TabPanel(props: TabPanelProps) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`tabpanel-${index}`}
      aria-labelledby={`tab-${index}`}
      style={{ marginTop: '20px' }}
      {...other}
    >
      {value === index && <Box>{children}</Box>}
    </div>
  );
}

// a ch mu
const sampleAddresses = [
  {
    id: 1,
    name: 'Nguyn Vn A',
    phone: '0987654321',
    address: '123 ng L Li, Phng Bn Ngh, Qun 1',
    city: 'TP. H Ch Minh',
    isDefault: true
  },
  {
    id: 2,
    name: 'Nguyn Vn A',
    phone: '0987654321',
    address: '456 ng Nguyn Hu, Phng Bn Ngh, Qun 1',
    city: 'TP. H Ch Minh',
    isDefault: false
  }
];

const AccountSettingsPage: React.FC = () => {
  const { user, updateUserInfo, isAuthenticated, loading } = useAuth();
  const navigate = useNavigate();
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));
  const [tabValue, setTabValue] = useState(0);
  const [showPassword, setShowPassword] = useState(false);
  const [showNewPassword, setShowNewPassword] = useState(false);
  const [showConfirmPassword, setShowConfirmPassword] = useState(false);
  const [selectedAddress, setSelectedAddress] = useState<number | null>(null);
  const [editingAddress, setEditingAddress] = useState<number | null>(null);
  const [addresses, setAddresses] = useState(sampleAddresses);
  
  // S dng custom hook thay v state ring l
  const { showSuccess, showError } = useSnackbar();

  // Form states
  const [personalInfo, setPersonalInfo] = useState({
    fullName: user?.name || '',
    email: user?.email || '',
    phone: user?.phone || '',
    birthday: user?.birthday || '',
    address: user?.address || '',
  });

  const [passwordForm, setPasswordForm] = useState({
    currentPassword: '',
    newPassword: '',
    confirmPassword: '',
  });

  const [newAddress, setNewAddress] = useState({
    name: '',
    phone: '',
    address: '',
    city: '',
    isDefault: false
  });

  const [notificationSettings, setNotificationSettings] = useState({
    orderUpdates: true,
    promotions: true,
    newArrivals: false,
    priceDrops: true
  });

  // Editing states
  const [isEditing, setIsEditing] = useState(false);
  const [passwordVisible, setPasswordVisible] = useState({
    currentPassword: false,
    newPassword: false,
    confirmPassword: false,
  });

  // Redirect to login if not authenticated
  useEffect(() => {
    if (!loading && !isAuthenticated) {
      navigate('/login', { state: { from: { pathname: '/account/settings' } } });
    }
  }, [isAuthenticated, loading, navigate]);

  // Update profile form data when user state changes
  useEffect(() => {
    if (user) {
      setPersonalInfo({
        fullName: user.name || '',
        email: user.email || '',
        phone: user.phone || '',
        birthday: user.birthday || '',
        address: user.address || '',
      });
    }
  }, [user]);

  // Handlers
  const handleTabChange = (event: React.SyntheticEvent, newValue: number) => {
    setTabValue(newValue);
  };

  const handleTogglePassword = (field: 'currentPassword' | 'newPassword' | 'confirmPassword') => {
    setPasswordVisible(prev => ({
      ...prev,
      [field]: !prev[field],
    }));
  };

  const handlePersonalInfoChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setPersonalInfo(prev => ({
      ...prev,
      [name]: value,
    }));
  };

  const handlePasswordChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setPasswordForm(prev => ({
      ...prev,
      [name]: value,
    }));
  };

  const handleNotificationChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setNotificationSettings({
      ...notificationSettings,
      [e.target.name]: e.target.checked
    });
  };

  const handleNewAddressChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.type === 'checkbox' ? e.target.checked : e.target.value;
    setNewAddress({
      ...newAddress,
      [e.target.name]: value
    });
  };

  const handleAddAddress = () => {
    if (!newAddress.name || !newAddress.phone || !newAddress.address || !newAddress.city) {
      showError('Vui lng in y  thng tin a ch');
      return;
    }

    const newAddressItem = {
      id: Date.now(),
      ...newAddress
    };

    // Nu a ch mi l mc nh, cp nht cc a ch khc thnh khng mc nh
    let updatedAddresses = addresses;
    if (newAddress.isDefault) {
      updatedAddresses = addresses.map(addr => ({
        ...addr,
        isDefault: false
      }));
    }

    setAddresses([...updatedAddresses, newAddressItem]);
    setNewAddress({
      name: '',
      phone: '',
      address: '',
      city: '',
      isDefault: false
    });
    showSuccess('Thm a ch mi thnh cng');
  };

  const handleDeleteAddress = (id: number) => {
    setAddresses(addresses.filter(addr => addr.id !== id));
    showSuccess(' xa a ch');
  };

  const handleSetDefaultAddress = (id: number) => {
    setAddresses(addresses.map(addr => ({
      ...addr,
      isDefault: addr.id === id
    })));
    showSuccess(' cp nht a ch mc nh');
  };

  const handleSavePersonalInfo = () => {
    // Thc t s gi API cp nht thng tin
    showSuccess(' cp nht thng tin c nhn');
  };

  const handleChangePassword = () => {
    if (!passwordForm.currentPassword || !passwordForm.newPassword || !passwordForm.confirmPassword) {
      showError('Vui lng in y  thng tin mt khu');
      return;
    }

    if (passwordForm.newPassword !== passwordForm.confirmPassword) {
      showError('Mt khu mi khng khp');
      return;
    }

    // Thc t s gi API i mt khu
    setPasswordForm({
      currentPassword: '',
      newPassword: '',
      confirmPassword: ''
    });
    showSuccess(' cp nht mt khu');
  };

  const handleSaveNotifications = () => {
    // Thc t s gi API cp nht ci t thng bo
    showSuccess(' cp nht ci t thng bo');
  };

  // Toggle edit mode
  const toggleEditMode = () => {
    setIsEditing(!isEditing);
    if (isEditing) {
      // Reset form if canceling edit
      setPersonalInfo({
        fullName: user?.name || '',
        email: user?.email || '',
        phone: user?.phone || '',
        birthday: user?.birthday || '',
        address: user?.address || '',
      });
    }
  };

  // Show loading spinner while checking authentication
  if (loading) {
    return (
      <Container sx={{ display: 'flex', justifyContent: 'center', py: 8 }}>
        <CircularProgress />
      </Container>
    );
  }
  
  // Don't render content if not authenticated (will redirect)
  if (!isAuthenticated) {
    return null;
  }

  return (
    <Container maxWidth="lg" sx={{ py: 5 }}>
      <Typography 
        variant="h4" 
        component="h1" 
        gutterBottom
        sx={{ 
          mb: 4, 
          fontWeight: 600,
          display: 'flex',
          alignItems: 'center',
          gap: 1
        }}
      >
        <Person fontSize="large" /> Ci t ti khon
      </Typography>
      
      <Grid container spacing={4}>
        {/* Sidebar vi avatar v tabs trn desktop */}
        {!isMobile && (
          <Grid item xs={12} md={3}>
            <Paper sx={{ p: 3, borderRadius: 2, textAlign: 'center', mb: 3 }}>
              <Avatar
                sx={{
                  width: 100,
                  height: 100,
                  margin: '0 auto 16px',
                  bgcolor: theme.palette.primary.main
                }}
              >
                <Person sx={{ fontSize: 60 }} />
              </Avatar>
              <Typography variant="h6" gutterBottom>
                {personalInfo.fullName}
              </Typography>
              <Typography variant="body2" color="text.secondary" gutterBottom>
                {personalInfo.email}
              </Typography>
              <Button
                startIcon={<Edit />}
                size="small"
                variant="outlined"
                sx={{ mt: 1 }}
              >
                i nh
              </Button>
            </Paper>

            <Paper sx={{ borderRadius: 2 }}>
              <Tabs
                orientation="vertical"
                variant="scrollable"
                value={tabValue}
                onChange={handleTabChange}
                sx={{
                  '& .MuiTab-root': {
                    alignItems: 'flex-start',
                    textAlign: 'left',
                    py: 2
                  }
                }}
              >
                <Tab label="Thng tin c nhn" />
                <Tab label="a ch giao hng" />
                <Tab label="i mt khu" />
                <Tab label="Ci t thng bo" />
              </Tabs>
            </Paper>
          </Grid>
        )}

        {/* Tabs responsive cho mobile */}
        {isMobile && (
          <Grid item xs={12}>
            <Paper sx={{ mb: 3, borderRadius: 2 }}>
              <Tabs
                value={tabValue}
                onChange={handleTabChange}
                variant="scrollable"
                scrollButtons="auto"
              >
                <Tab label="Thng tin" />
                <Tab label="a ch" />
                <Tab label="Mt khu" />
                <Tab label="Thng bo" />
              </Tabs>
            </Paper>
          </Grid>
        )}

        {/* Tab content */}
        <Grid item xs={12} md={9}>
          <Paper sx={{ p: 3, borderRadius: 2 }}>
            <TabPanel value={tabValue} index={0}>
              <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 3 }}>
                <Typography variant="h5" sx={{ fontWeight: 600 }}>
                  Thng tin c nhn
                </Typography>
                <Button
                  startIcon={isEditing ? <Save /> : <Edit />}
                  onClick={toggleEditMode}
                  variant={isEditing ? "contained" : "outlined"}
                >
                  {isEditing ? "Lu thay i" : "Chnh sa"}
                </Button>
              </Box>
              
              <form onSubmit={handleSavePersonalInfo}>
                <Grid container spacing={3}>
                  <Grid item xs={12}>
                    <TextField
                      fullWidth
                      label="H v tn"
                      name="fullName"
                      value={personalInfo.fullName}
                      onChange={handlePersonalInfoChange}
                      InputProps={{
                        startAdornment: <Person sx={{ mr: 1, color: 'text.secondary' }} />,
                        readOnly: !isEditing,
                      }}
                      variant={isEditing ? "outlined" : "filled"}
                    />
                  </Grid>
                  <Grid item xs={12} sm={6}>
                    <TextField
                      fullWidth
                      label="Email"
                      type="email"
                      name="email"
                      value={personalInfo.email}
                      onChange={handlePersonalInfoChange}
                      InputProps={{
                        startAdornment: <Email sx={{ mr: 1, color: 'text.secondary' }} />,
                        readOnly: true, // Email khng th thay i
                      }}
                      variant="filled"
                      helperText="Email khng th thay i"
                    />
                  </Grid>
                  <Grid item xs={12} sm={6}>
                    <TextField
                      fullWidth
                      label="S in thoi"
                      name="phone"
                      value={personalInfo.phone}
                      onChange={handlePersonalInfoChange}
                      InputProps={{
                        startAdornment: <Phone sx={{ mr: 1, color: 'text.secondary' }} />,
                        readOnly: !isEditing,
                      }}
                      variant={isEditing ? "outlined" : "filled"}
                    />
                  </Grid>
                  <Grid item xs={12} sm={6}>
                    <TextField
                      fullWidth
                      label="Ngy sinh"
                      type="date"
                      name="birthday"
                      value={personalInfo.birthday}
                      onChange={handlePersonalInfoChange}
                      InputLabelProps={{
                        shrink: true,
                      }}
                    />
                  </Grid>
                  <Grid item xs={12}>
                    <TextField
                      fullWidth
                      label="a ch"
                      name="address"
                      value={personalInfo.address}
                      onChange={handlePersonalInfoChange}
                      InputProps={{
                        startAdornment: <Home sx={{ mr: 1, color: 'text.secondary' }} />,
                        readOnly: !isEditing,
                      }}
                      variant={isEditing ? "outlined" : "filled"}
                      multiline
                      rows={2}
                    />
                  </Grid>
                  
                  {isEditing && (
                    <Grid item xs={12}>
                      <Box sx={{ display: 'flex', justifyContent: 'flex-end', gap: 2, mt: 2 }}>
                        <Button variant="outlined" onClick={toggleEditMode}>
                          Hy
                        </Button>
                        <Button variant="contained" type="submit">
                          Lu thay i
                        </Button>
                      </Box>
                    </Grid>
                  )}
                </Grid>
              </form>
            </TabPanel>

            <TabPanel value={tabValue} index={1}>
              <Typography variant="h6" gutterBottom>
                a ch giao hng
              </Typography>
              <Divider sx={{ mb: 3 }} />

              {/* Danh sch a ch */}
              <List sx={{ mb: 4 }}>
                {addresses.map((address) => (
                  <Paper
                    key={address.id}
                    elevation={1}
                    sx={{
                      mb: 2,
                      p: 2,
                      border: address.isDefault ? `1px solid ${theme.palette.primary.main}` : 'none',
                      borderRadius: 2,
                      position: 'relative',
                      '&::before': address.isDefault ? {
                        content: '"Mc nh"',
                        position: 'absolute',
                        top: -10,
                        right: 20,
                        backgroundColor: theme.palette.primary.main,
                        color: 'white',
                        px: 1,
                        py: 0.5,
                        borderRadius: 1,
                        fontSize: '0.75rem'
                      } : {}
                    }}
                  >
                    <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1 }}>
                      <Typography variant="subtitle1" sx={{ fontWeight: 600 }}>
                        {address.name} | {address.phone}
                      </Typography>
                      <Box>
                        {!address.isDefault && (
                          <IconButton
                            size="small"
                            onClick={() => handleSetDefaultAddress(address.id)}
                            color="primary"
                          >
                            <Check fontSize="small" />
                          </IconButton>
                        )}
                        <IconButton
                          size="small"
                          onClick={() => handleDeleteAddress(address.id)}
                          color="error"
                        >
                          <Delete fontSize="small" />
                        </IconButton>
                      </Box>
                    </Box>
                    <Typography variant="body2" color="text.secondary">
                      {address.address}, {address.city}
                    </Typography>
                  </Paper>
                ))}
              </List>

              {/* Form thm a ch mi */}
              <Paper elevation={1} sx={{ p: 2, borderRadius: 2, mb: 2 }}>
                <Typography variant="h6" gutterBottom>
                  Thm a ch mi
                </Typography>
                <Grid container spacing={2}>
                  <Grid item xs={12} sm={6}>
                    <TextField
                      fullWidth
                      label="H tn"
                      name="name"
                      value={newAddress.name}
                      onChange={handleNewAddressChange}
                    />
                  </Grid>
                  <Grid item xs={12} sm={6}>
                    <TextField
                      fullWidth
                      label="S in thoi"
                      name="phone"
                      value={newAddress.phone}
                      onChange={handleNewAddressChange}
                    />
                  </Grid>
                  <Grid item xs={12}>
                    <TextField
                      fullWidth
                      label="a ch"
                      name="address"
                      value={newAddress.address}
                      onChange={handleNewAddressChange}
                    />
                  </Grid>
                  <Grid item xs={12} sm={6}>
                    <TextField
                      fullWidth
                      label="Tnh/Thnh ph"
                      name="city"
                      value={newAddress.city}
                      onChange={handleNewAddressChange}
                    />
                  </Grid>
                  <Grid item xs={12}>
                    <FormControlLabel
                      control={
                        <Switch
                          checked={newAddress.isDefault}
                          onChange={handleNewAddressChange}
                          name="isDefault"
                          color="primary"
                        />
                      }
                      label="t lm a ch mc nh"
                    />
                  </Grid>
                </Grid>
                <Box sx={{ mt: 2, textAlign: 'right' }}>
                  <Button
                    variant="contained"
                    color="primary"
                    onClick={handleAddAddress}
                    startIcon={<Add />}
                  >
                    Thm a ch
                  </Button>
                </Box>
              </Paper>
            </TabPanel>

            <TabPanel value={tabValue} index={2}>
              <Typography variant="h6" gutterBottom>
                i mt khu
              </Typography>
              <Divider sx={{ mb: 3 }} />

              <form onSubmit={handleChangePassword}>
                <Stack spacing={3}>
                  <TextField
                    fullWidth
                    label="Mt khu hin ti"
                    name="currentPassword"
                    type={passwordVisible.currentPassword ? 'text' : 'password'}
                    value={passwordForm.currentPassword}
                    onChange={handlePasswordChange}
                    required
                    InputProps={{
                      endAdornment: (
                        <IconButton onClick={() => handleTogglePassword('currentPassword')}>
                          {passwordVisible.currentPassword ? <VisibilityOff /> : <Visibility />}
                        </IconButton>
                      ),
                    }}
                  />
                  
                  <TextField
                    fullWidth
                    label="Mt khu mi"
                    name="newPassword"
                    type={passwordVisible.newPassword ? 'text' : 'password'}
                    value={passwordForm.newPassword}
                    onChange={handlePasswordChange}
                    required
                    InputProps={{
                      endAdornment: (
                        <IconButton onClick={() => handleTogglePassword('newPassword')}>
                          {passwordVisible.newPassword ? <VisibilityOff /> : <Visibility />}
                        </IconButton>
                      ),
                    }}
                  />
                  
                  <TextField
                    fullWidth
                    label="Xc nhn mt khu mi"
                    name="confirmPassword"
                    type={passwordVisible.confirmPassword ? 'text' : 'password'}
                    value={passwordForm.confirmPassword}
                    onChange={handlePasswordChange}
                    required
                    InputProps={{
                      endAdornment: (
                        <IconButton onClick={() => handleTogglePassword('confirmPassword')}>
                          {passwordVisible.confirmPassword ? <VisibilityOff /> : <Visibility />}
                        </IconButton>
                      ),
                    }}
                  />
                  
                  <Button 
                    variant="contained" 
                    color="primary" 
                    type="submit"
                    sx={{ alignSelf: 'flex-start', mt: 2 }}
                  >
                    Cp nht mt khu
                  </Button>
                </Stack>
              </form>
            </TabPanel>

            <TabPanel value={tabValue} index={3}>
              <Typography variant="h6" gutterBottom>
                Ci t thng bo
              </Typography>
              <Divider sx={{ mb: 3 }} />

              <List>
                <ListItem sx={{ px: 0 }}>
                  <ListItemText
                    primary="Cp nht n hng"
                    secondary="Thng bo v trng thi n hng, giao hng v hon tin"
                  />
                  <ListItemSecondaryAction>
                    <Switch
                      edge="end"
                      checked={notificationSettings.orderUpdates}
                      onChange={handleNotificationChange}
                      name="orderUpdates"
                    />
                  </ListItemSecondaryAction>
                </ListItem>
                <Divider />
                <ListItem sx={{ px: 0 }}>
                  <ListItemText
                    primary="Khuyn mi"
                    secondary="Thng bo v cc chng trnh khuyn mi, gim gi v m voucher"
                  />
                  <ListItemSecondaryAction>
                    <Switch
                      edge="end"
                      checked={notificationSettings.promotions}
                      onChange={handleNotificationChange}
                      name="promotions"
                    />
                  </ListItemSecondaryAction>
                </ListItem>
                <Divider />
                <ListItem sx={{ px: 0 }}>
                  <ListItemText
                    primary="Sn phm mi"
                    secondary="Thng bo khi c sn phm mi ra mt"
                  />
                  <ListItemSecondaryAction>
                    <Switch
                      edge="end"
                      checked={notificationSettings.newArrivals}
                      onChange={handleNotificationChange}
                      name="newArrivals"
                    />
                  </ListItemSecondaryAction>
                </ListItem>
                <Divider />
                <ListItem sx={{ px: 0 }}>
                  <ListItemText
                    primary="Gim gi sn phm"
                    secondary="Thng bo khi sn phm trong danh sch yu thch gim gi"
                  />
                  <ListItemSecondaryAction>
                    <Switch
                      edge="end"
                      checked={notificationSettings.priceDrops}
                      onChange={handleNotificationChange}
                      name="priceDrops"
                    />
                  </ListItemSecondaryAction>
                </ListItem>
              </List>

              <Box sx={{ mt: 3, textAlign: 'right' }}>
                <Button
                  variant="contained"
                  color="primary"
                  onClick={handleSaveNotifications}
                >
                  Lu ci t
                </Button>
              </Box>
            </TabPanel>
          </Paper>
        </Grid>
      </Grid>
    </Container>
  );
};

export default AccountSettingsPage; 