import { Product } from '../types';

const categories = ['men', 'women', 'accessories'];
const colors = ['en', 'Trng', 'Xm', '', 'Xanh dng', 'Xanh l', 'Vng', 'Hng'];
const sizes = ['S', 'M', 'L', 'XL', 'XXL'];
const productNames = [
  'o thun nam',
  'o s mi nam',
  'Qun jean nam',
  'o khoc nam',
  'o thun n',
  'o s mi n',
  'Qun jean n',
  'o khoc n',
  'Ti xch',
  'V da',
  'Tht lng',
  'Khn qung',
];

export const generateProducts = (count: number): Product[] => {
  return Array.from({ length: count }, (_, index) => {
    const category = categories[Math.floor(Math.random() * categories.length)];
    const name = productNames[Math.floor(Math.random() * productNames.length)];
    const price = Math.floor(Math.random() * (1000000 - 100000) + 100000);
    const originalPrice = Math.random() > 0.5 ? price * 1.5 : undefined;
    const rating = Math.floor(Math.random() * 2) + 4; // 4-5 stars
    const reviewCount = Math.floor(Math.random() * 1000);
    const productColors = Array.from(
      { length: Math.floor(Math.random() * 3) + 1 },
      () => colors[Math.floor(Math.random() * colors.length)]
    );
    const productSizes = Array.from(
      { length: Math.floor(Math.random() * 3) + 2 },
      () => sizes[Math.floor(Math.random() * sizes.length)]
    );

    return {
      id: `product-${index + 1}`,
      name: `${name} ${index + 1}`,
      price,
      originalPrice,
      image: `https://picsum.photos/seed/${index}/400/500`,
      rating,
      reviewCount,
      description: `M t chi tit cho sn phm ${name} ${index + 1}. y l mt sn phm cht lng cao, c thit k vi phong cch hin i v thi trang.`,
      category,
      colors: productColors,
      sizes: productSizes,
      images: Array.from(
        { length: 4 },
        (_, imgIndex) => `https://picsum.photos/seed/${index}-${imgIndex}/400/500`
      ),
      inStock: Math.random() > 0.1, // 90% sn phm c sn
      createdAt: new Date().toISOString(),
      updatedAt: new Date().toISOString(),
    };
  });
}; 